"use strict";

import PopoutView from './views/PopoutView';
import ListPanelView from './views/ListPanelView';
import MenuItemView from './views/MenuItemView';
import DefaultButtonView from './views/DefaultButtonView';
import MenuPanelView from './views/MenuPanelView';
import DropdownView from './views/DropdownView';

/**
 * The factory covers common use cases of Popout/Dropdown views.
 * You can create a simple menu (and other stuff like this) in one click without lots of manual work.
 * @namespace factory
 * @memberof module:core.dropdown
 * */

export default /** @lends module:core.dropdown.factory */ {
    /**
     * @description         {@link module:core.dropdown.views.PopoutView PopoutView}.
     *                <code>buttonView</code>  <code>panelView</code> (      )
     *                View  .   PopoutView  'as is'.
     * @param {Object} options   {@link module:core.dropdown.views.PopoutView PopoutView}.   ,  :
     * @param {Array} options.items   .        <code>{ id, name }</code>
     *                                Backbone.Collection.
     * @param {String} [options.text]   .   ,     <code>buttonView</code>.
     * @returns {Backbone.View} View 
     * */
    createMenu: function(options) {
        options = options || {};
        var collection = options.items;
        if (!(collection instanceof Backbone.Collection)) {
            collection = new Backbone.Collection(collection);
        }

        var effectiveButtonModel = options.buttonModel || new Backbone.Model({
            text: options.text
        });

        if (!options.buttonModel) {
            var defaultActModel = collection.findWhere({ default: true });
            if (defaultActModel) {
                effectiveButtonModel = defaultActModel;
                collection.remove(effectiveButtonModel);
            }
        }

        return this.createPopout(_.extend({
            buttonView: DefaultButtonView,
            buttonViewOptions: {
                model: effectiveButtonModel
            },
            panelView: MenuPanelView,
            panelViewOptions: {
                collection: collection
            }
        }, options));
    },

    /**
     * @description     {@link module:core.dropdown.views.PopoutView PopoutView}   .
     *                     ,    .
     *                 <code>{ fade: true, height: 'bottom' }</code>.
     * @param {Object} options   {@link module:core.dropdown.views.PopoutView PopoutView}
     * @returns {PopoutView}  PopoutView
     * */
    createDialogPopout: function (options) {
        var defaults = {
            fade: true,
            height: 'bottom'
        };
        options = _.extend(defaults, options);
        return this.createPopout(options);
    },

    /**
     * @description    
     *              {@link module:core.dropdown.views.PopoutView PopoutView}    'as is'.
     * @param {Object} options   {@link module:core.dropdown.views.PopoutView PopoutView}
     * @returns {PopoutView}  PopoutView
     * */
    createPopout: function (options) {
        return new PopoutView(options);
    },

    /**
     * @description    
     *              {@link module:core.dropdown.views.DropdownView DropdownView}    'as is'.
     * @param {Object} options   {@link module:core.dropdown.views.DropdownView DropdownView}
     * @returns {DropdownView}  DropdownView
     * */
    createDropdown: function (options) {
        return new DropdownView(options);
    }
};



